#button {
    display: inline-block;
    color: rgb(48, 47, 47);
    padding: 10px 20px;
    font-size: 16px;
    font-weight: 500;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    border: 2px solid #3498db;
    /* color: #3498db; */
    background-color: rgb(147, 147, 237);
    border-radius: 4px;
    transition: background-color 0.3s, color 0.3s, border-color 0.3s;
  
  }
  
  #button:hover {
    background-color: #3498db;
    color: #ffffff;
    border-color: #ffffff;
  }
